<template lang='pug'>
    .login.clearfix
        .login-left
            .logo
        .login-right
            .logo
            .form
                .form-btn
                    button(:class='loginType==0?"active":""' @click='changeType(0)').btn1 
                    button(:class='loginType==1?"active":""' @click='changeType(1)').btn2 
                .form-box(v-if="loginType==0")
                    input(placeholder='EC,EC-xxxx-xxxx-xxxx-xxxxx' 
                    v-model='account'
                    @keyup.enter='ecLogin'
                    @change='saveECAccount')
                    button(@click='ecLogin') EC
                .form-box(v-if="loginType==1")
                    input(placeholder='' 
                    v-model='password' 
                    type='password'
                    @keyup.enter='pwdLogin')
                    button(@click='pwdLogin') EC
            .remenber
                .select.clearfix
                    .sel-all(@click='remen')
                        .sel-box(:class='remenber?"active":""')
                        span 
                    router-link(to='/autoreg' tag='a').go-reg 
        

        transition(name='fade')
            #ss-mask(v-if='boxShow')
                #ss-mask-alert
                    #ss-mask-alert-tit 
                        span 
                        a#ss-mask-alert-tit-close(href='javascript:;' @click='close') 
                    #ss-mask-alert-content
                        p
                            i(:class='type+" iconfont icon-xinxi"')
                            | {{content}}
                    #ss-mask-alert-enter
                        button#ss-btn(@click='close')
                            span 
        
</template>

<script>
import '$assets/scss/user/login.scss'
import { getEC,postEC } from '@/config/http.js'
// import msgBox from '$com/public/msgBox'

import '@/assets/scss/public/season-msgbox.scss'
import '@/assets/scss/public/season-button.scss'



export default {
    created() {
        if(sessionStorage.getItem('loginState')=='false') {
            this.alertBox('danger','');
        }
        if(localStorage.getItem('ECAddress')) {
            this.remenber=true;
            this.account=localStorage.getItem('ECAddress');
        }
    },
    data() {
        return {
            remenber:false,
            // 
            boxShow:false,
            type:'info',
            content:'',

            loginType:0,
            account:'',//
            password:'',//
        }
    },
    methods: {
        saveECAccount() {
            if(this.remenber) {
                localStorage.setItem('ECAddress',this.account);
            }
        },
        close() {
            this.boxShow=false;
        },
        remen() {
            this.remenber=!this.remenber;
            if(this.remenber==true) {
                localStorage.setItem('ECAddress',this.account);
            }else {
                localStorage.removeItem('ECAddress');
            }
        },
        changeType(type) {
            this.loginType = type;
        },
        alertBox(type,content) {
            this.type=type||'info',
            this.boxShow=true;
            this.content=content;
        },
        async ecLogin() { // EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccount',
                    account: this.account,
                    random: Math.random()
                })
                if(this.account.length<10) {
                    this.alertBox('danger','');
                    return false;
                }
                if(res.data.errorCode==3) {
                    this.alertBox('danger','');
                    return false;
                }if(res.data.errorCode==4) {
                    this.alertBox('danger','EC');
                    return false;
                }
                if(res.data.errorCode==5) {
                    sessionStorage.setItem('noPubKey',1); //
                }
                sessionStorage.setItem('loginState',true);
                sessionStorage.setItem('account',res.data.account); //
                sessionStorage.setItem('accountRS',res.data.accountRS); //
                sessionStorage.setItem('loginType','account'); //
                this.$router.push('/home')
                
            }
            catch (e) {
                console.log(e);
            }
        },
        async pwdLogin() { // EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.password
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //publicKey
                if(res.data.errorCode==3) {
                    this.alertBox('danger','');
                    return false;
                }else {
                    sessionStorage.setItem('loginState',true);
                    sessionStorage.setItem('account',res.data.account); //
                    sessionStorage.setItem('accountRS',res.data.accountRS); //
                    sessionStorage.setItem('publicKey',res.data.publicKey); //
                    sessionStorage.setItem('loginType','password');
                    this.getAccount(sessionStorage.getItem('account'));
                    this.$router.push('/home')
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async getAccount(account) { // EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccount',
                    account:account,
                    includeAssets:true,
                    includeCurrencies:true,
                    includeLessors:true,
                    includeEffectiveBalance:true,
                    random:Math.random()
                })
                if(res.data.errorCode==5) {
                    sessionStorage.setItem('noPubKey',1); //
                }else {
                    this.startDrag(account);
                }

            }
            catch (e) {
                console.log(e);
            }
        },
        async startDrag(account) { // EC
            try {
                let res = await postEC({ 
                    requestType: 'startForging',
                    secretPhrase:account
                })
                if(res.data.errorCode) {
                    console.log(res.data.errorCode);
                }else {
                    console.log('...');
                    sessionStorage.setItem('isDrag',1);//
                }

            }
            catch (e) {
                console.log(e);
            }
        },
    }
}
</script>



