<template lang='pug'>
    .autoreg.clearfix
        transition(name='fade')
            #ss-mask(v-if='boxShow')
                #ss-mask-alert
                    #ss-mask-alert-tit 
                        span 
                        a#ss-mask-alert-tit-close(href='javascript:;' @click='close') 
                    #ss-mask-alert-content
                        p
                            i(:class='type+" iconfont icon-xinxi"')
                            | {{content}}
                    #ss-mask-alert-enter
                        button#ss-btn(@click='close')
                            span 
        .autoreg-left
            .logo
        .autoreg-right
            .logo
            .form(v-if='!havePwd')
                .form-tit
                    h4 EC
                .form-txt
                    p 
                .form-code
                    p {{pwd}}
                .form-tip
                    p 
                        //- i.iconfont.icon-xinxi
                        |  EC 
                .form-tip.danger
                    p
                        i.iconfont.icon-xinxi
                        | 
            .form(v-if='havePwd')
                .form-tit
                    h4 
                .form-txt
                    p 
                .form-txt
                    input(v-model='password')
                .form-tip
                    p 
                        //- i.iconfont.icon-xinxi
                        |  EC 
                .form-tip.danger
                    p
                        i.iconfont.icon-xinxi
                        | 
            .autoreg-right-btn.clearfix(v-if='!havePwd')
                button(@click='goNext').next 
                button.cancel(@click='cancel') 
            .autoreg-right-btn.clearfix(v-if='havePwd')
                button.next(@click='checkLogin') 
                button.cancel(@click='cancel') 
        //- .autoreg-right
        //-     .check-login
        //-         input(placeholder='')
            //- .autoreg-right-goreg
            //-     router-link(to='/autoreg' tag='a').go-reg 
        
</template>

<script>
import '$assets/scss/user/autoreg.scss'
import md5 from 'js-md5'
import { getEC,postEC } from '@/config/http.js'

export default {
    created() {
        let a = Date.parse(new Date);
        let b = Math.random().toString().slice(2,10);
        this.pwd = 'EC-'+md5(a+b).toUpperCase();
    },
    data() {
        return {
            remenber:false,
            loginType:0,
            account:'123',
            pwd:'',//
            password:'',//
            havePwd:false,
            // 
            boxShow:false,
            type:'info',
            content:'',
        }
    },
    methods: {
        cancel() {
            this.$router.push('/login');
        },
        close() {
            this.boxShow=false;
        },
        goNext() {
            this.havePwd = true;
        },
        alertBox(type,content) {
            this.type=type||'info',
            this.boxShow=true;
            this.content=content;
        },
        remen() {
            this.remenber=!this.remenber;
        },
        changeType(type) {
            this.loginType = type;
        },
        goLogin() {
            console.log(this.account)
        },
        checkLogin() {
            if(this.pwd!=this.password) {
            this.alertBox('danger','');
            this.havePwd=false;
            }else {
                this.pwdLogin();
            }
        },
        async pwdLogin() { // EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.password
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //publicKey
                if(res.data.errorCode==3) {
                    this.alertBox('danger','');
                    return false;
                }else {
                    sessionStorage.setItem('loginState',true);
                    sessionStorage.setItem('account',res.data.account); //
                    sessionStorage.setItem('accountRS',res.data.accountRS); //
                    sessionStorage.setItem('publicKey',res.data.publicKey); //
                    sessionStorage.setItem('loginType','password');
                    this.getAccount(sessionStorage.getItem('account'));
                    this.$router.push('/home')
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async getAccount(account) { // EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccount',
                    account:account,
                    includeAssets:true,
                    includeCurrencies:true,
                    includeLessors:true,
                    includeEffectiveBalance:true,
                    random:Math.random()
                })
                if(res.data.errorCode==5) {
                    sessionStorage.setItem('noPubKey',1); //
                }

            }
            catch (e) {
                console.log(e);
            }
        },
    },
    
}
</script>



