<template lang='pug'>
    .list
        table.title.clearfix
            thead
                tr
                    th 
                    th 
                    th 
                    th 
                    th 
            tbody
                item(v-for='(item,key) in dataList.generators' :key='key' :timestamp='dataList.timestamp' :data='item')
        
</template>

<script>
import '$assets/scss/home/list.scss'
import item from '$com/Gen/item'

export default {
    props:['dataList'],
    components:{
        item
        // homeNav
    }
}
</script>



